.header {
    background-color: #2196F3;
    height: 65px;
    display: flex;
    align-items: center;
}

.header__nav {
    color: white;
    text-decoration: none;
    margin-left: 20px;
}

.cat {
    position: relative;
}

.cat img {
    margin-top: 50px;
    width: 300px;
    height: 300px;
}

.cats__inner {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}

.btn {
    display: none;
    border: none;
    width: 50px;
    height: 50px;
    background-color: #F24E1E;
    right: 10px;
    bottom: 20px;
    position: absolute;
}

.cat:hover .btn {
    display: block;
}